import {row, col, css} from "../utils"

export class Block {
	constructor(value, options) {
		this.value = value
		this.options = options
	}

	toHTML() {
		throw new Error('–ú–µ—Ç–æ–¥ toHTML –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å —Ä–µ–∞–ª–∏–∑–æ–≤–∞–Ω!')
	}
}

export class Title extends Block {
	constructor(value, options) {
		super(value, options)
	}

	toHTML() {
		const {tag = 'h1', styles} = this.options
		return row(col(`<${tag}>${this.value}</${tag}>`), css(styles))
	}
}

export class Image extends Block {
	constructor(value, options) {
		super(value, options)
	}

	toHTML() {
		const {styles, imageStyles : is, alt = ""} = this.options
		return row(`<img src="${this.value}" alt="${alt}" style="${css(is)}"`, css(styles))
	}
}

export class Columns extends Block {
	constructor(value, options) {
		super(value, options)
	}

	toHTML() {
		const {styles} = this.options
		const html = this.value.map(col).join("") // e=> col(e) == col
		return row(html, css(styles))
	}
}

export class Text extends Block {
	constructor(value, options) {
		super(value, options)
	}

	toHTML() {
		const {styles} = this.options
		return row(col(`<p>${this.value}</p>`), css(styles))
	}
}
